.RepCards {
    flex: 1;
    display: flex;
    flex-direction: column;
    background: rgba(255,255,255, 0.8);
}

.RepCardsTitle{
    flex: 0.1;
    padding: 10px;
    box-shadow: 0 3px 10px rgb(0 0 0 / 0.2);
}

.RepCardsTitleH1{
    font-weight: 600;
    background-image: linear-gradient(to left, #46585c, #31383a);
    color: transparent;
    background-clip: text;
    -webkit-background-clip: text;
}

.RepCardsBody{
    margin: 10px;
    display: flex;
    flex-flow: row wrap;
}

.card{
    background-color: beige !important;
    margin: 10px;
}

.circularProgressBar{
    padding: 10px;
}