.App {
  text-align: center;
  display: none;
  flex-direction:row;
}

.App-logo {
  max-height: 100px;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  color: white;
  flex: 0.2;
  max-width: 200px;
}

.App-menu{
  display: flex;
  flex-direction: column;
  margin-top: 20px;
  width: 90%;
  flex: 1;
}

.App-link {
  cursor: pointer;
  padding: 10px;
  font-size: 18px;
}

.App-link-selected{
  color: #61dafb;
  box-shadow: 0 -5px 3px -3px black, 0 5px 3px -3px;
}

img{
  margin: 10px;
}

.App-menu-footer{
  margin-bottom: 20px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
